import React, { useState } from "react";
import { makeStyles } from "@material-ui/styles";
import { Nav } from "react-bootstrap";
// import { RiMenu3Line, RiCloseLine } from 'react-icons/ri';
import Button from "react-bootstrap/Button";
import Styling from "../../scripts/models";

const useStyles = makeStyles(() => ({
  activeButton: {}
}));

export default function Filter() {
  const classes = useStyles();
  const [activeButton, setActiveButton] = useState("first");

  const clickedButtonHandler = (e) => {
    const { name } = e.target;
    setActiveButton(name);
  };

  return (
      <>
      <Nav.Item>
          <Button name="first"
              type="button" id="buttonFont1"
              className={activeButton === "first" ? `active` : ""}
              variant="light" onClick={(e) => {clickedButtonHandler(e); Styling.setSizeOne();}}>A</Button>
      </Nav.Item>
        <Nav.Item>
            <Button name="second"
                type="button" id="buttonFont2"
                className={activeButton === "second" ? `active` : ""}
                variant="light" onClick={(e) => {clickedButtonHandler(e); Styling.setSizeThree();}}>A</Button>
        </Nav.Item>
        <Nav.Item>
            <Button name="third"
                type="button" id="buttonFont3"
                className={activeButton === "third" ? `active` : ""}
                variant="light" onClick={(e) => {clickedButtonHandler(e); Styling.setSizeTwo();}}>A</Button>
        </Nav.Item>
      </>
  );
}

